.main-use {
  position: relative;
  padding-top: 14.0625em;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 14.0625em;
    background-image: url("../img/main-use__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    right: -5.25em;
    width: 61.5em;
    height: 61.5em;
    background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(67, 182, 75, 0.22) 0%, rgba(67, 182, 75, 0.00) 100%);
    border-radius: 61.5em;
    pointer-events: none;
  }
  &__inner {
    background-image: url("../img/main-use__bg2.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    padding: 3.75em 0;
  }
  &__img {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    width: 39em;
    img {
      width: 100%;
    }
  }
  &__content {
    position: relative;
    z-index: 3;
  }
  &__title {
    margin-top: 1.5625em;
    &-text {
      font-size: 1.875em;
      line-height: 140%;
      font-weight: 500;
      color: #ffffff;
      span {
        display: block;
        color: #43B64B;
      }
    }
    &-icon {
      display: block;
      width: 2.375em;
      margin-top: 0.3125em;
    }
  }
  &__text {
    margin-top: 1.5625em;
    p {
      font-size: 1.25em;
      line-height: 140%;
      color: #ffffff;

    }
  }
}
.info-use {
  padding: 6.25em 0 3.125em;
  &__desc {
    margin-top: 2.1875em;
  }
  &__text {
    p {
      font-size: 0.9375em;
      line-height: 140%;
      color: #272B2D;
      &:not(:first-child) {
        margin-top: 1em;
      }
    }
  }
  &__list {
    margin-top: 1em;
    li {
      font-size: 0.9375em;
      line-height: 140%;
      color: #272B2D;
      padding-left: 1.875em;
      position: relative;
      &:not(:first-child) {
        margin-top: 0.5em;
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 1.25em;
        height: 0.0625em;
        background: #43B64B;
      }
    }
  }
}
.features-use {
  padding: 3.125em 0;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    left: -32.25em;
    bottom: -6.25em;
    width: 61.5em;
    height: 61.5em;
    background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(67, 182, 75, 0.22) 0%, rgba(67, 182, 75, 0.00) 100%);
    border-radius: 61.5em;
    pointer-events: none;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 3.75em;
    margin-top: 2.1875em;
    position: relative;
    z-index: 2;
    &:after {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 4.6875em;
      bottom: 0;
      width: 0.0625em;
      height: 100%;
      background: #43B64B;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    &-number {
      flex-shrink: 0;
      width: 9.375em;
      height: 9.375em;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      span {
        font-size: 3.75em;
        line-height: 140%;
        font-weight: 700;
        color: #43B64B;
      }
    }
    &-text {
      width: 65.625em;
      min-height: 5.1875em;
      display: flex;
      align-items: center;
      background: #ffffff;
      margin-left: 2.5em;
      padding: 0.625em 1em 0.625em 3.75em;
      p {
        font-size: 1.1875em;
        line-height: 140%;
        color: #000000;
      }
    }
  }
}
.gallery {
  padding: 3.125em 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 7.5em / 4));
    gap: 2.5em;
    margin-top: 2.1875em;
  }
  &__item {
    position: relative;
    padding-bottom: 66%;
    &:hover {
      transform: scale(1.1);
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
}
.info2-use {
  padding: 3.125em 0;
  position: relative;
  &__img {
    position: absolute;
    z-index: -1;
    top: -11.875em;
    right: 0;
    width: 14.4375em;
  }
  &__desc {
    margin-top: 2.1875em;
    p {
      font-size: 0.9375em;
      line-height: 140%;
      color: #272B2D;
      &:not(:first-child) {
        margin-top: 1em;
      }
    }
  }
}
.answers {
  padding: 3.125em 0 6.25em;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.1875em;
    margin-top: 2.1875em;
  }
  &__item {
    background: #ffffff;
    box-shadow: 0 0.625em 2.5em 0 rgba(67, 182, 75, 0.35);
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 2.5em 4.8125em;
      cursor: pointer;
      &--active {
        .answers__item-btn {
          transform: rotate(180deg);
        }
      }
    }
    &-hidden {
      padding: 0 4.8125em 1.875em;
      display: none;
    }
    &-title {
      span {
        font-size: 1.125em;
        line-height: 120%;
        font-weight: 700;
        color: #272B2D;
      }
    }
    &-btn {
      background: transparent;
      padding: 0;
      margin: 0;
      margin-left: 2em;
    }
    &-desc {
      padding-top: 1.875em;
      border-top: 0.0625em solid #BDBDBD;
      p {
        font-size: 1.125em;
        line-height: 120%;
        color: #303436;
      }
    }
  }
}
.banner {
  padding: 4em 0;
  background-image: url("../img/banner-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    max-width: 50.625em;
  }
  &__title {
    span {
      font-size: 2.1875em;
      line-height: 140%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffffff;
    }
  }
}
.advantages2 {
  padding: 6.25em 0 3.125em;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(67, 182, 75, 0.22) 0%, rgba(67, 182, 75, 0.00) 100%);
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.75em / 2));
    gap: 1.875em 3.75em;
    margin-top: 2.1875em;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
    padding: 1.125em 1.5625em;
    &-title {
      span {
        font-size: 1.25em;
        line-height: 140%;
        font-weight: 500;
        color: #272B2D;
      }
    }
    &-text {
      margin-top: 0.625em;
      p {
        font-size: 1.0625em;
        line-height: 140%;
        font-weight: 300;
        color: #272B2D;
      }
    }
    &-icon {
      margin-left: 1em;
    }
  }
}
.certificates {
  padding: 3.125em 0 6.25em;
  position: relative;
  &__bg {
    position: absolute;
    left: 0;
    bottom: 5em;
    width: 18.75em;
  }
  &__bg2 {
    position: absolute;
    right: 0;
    bottom: 5em;
    width: 18.75em;
  }
  &__slider {
    margin-top: 2.1875em;
  }
  @media screen and (min-width: 993px) {
    &__swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(5,calc((100% / 5) - (3.75em * 4 / 5)));
      gap: 3.75em;
      transform: unset !important;
    }
    .swiper-slide {
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  &-slide {
    img {
      width: 100%;
    }
  }
}


