.footer {
  padding: 2.8125em 0;
  &2 {
    border-top: 0.0625em solid #303436;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    display: block;
    width: 9.375em;
    img {
      width: 100%;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__nav {
    &:not(:first-child) {
      margin-left: 5em;
    }
    &-title {
      position: relative;
      padding-bottom: 0.4375em;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 3.125em;
        height: 0.0625em;
        background: #BDBDBD;
      }
      span {
        font-family: 'OpenSans';
        font-size: 0.9375em;
        line-height: 120%;
        color: #272B2D;
      }
    }
  }
  &__list {
    margin-top: 0.4375em;
    li {
      font-size: 0.9375em;
      line-height: 120%;
      color: #272B2D;
      &:not(:first-child) {
        margin-top: 0.625em;
      }
      &:hover {
        color: #43B64B;
      }
    }
  }
  &__brevis {
    max-width: 20.3125em;
    width: 100%;
    display: flex;
    align-items: center;
    &-img {
      flex-shrink: 0;
      width: 2.4375em;
      img {
        width: 100%;
      }
    }
    &-text {
      margin-left: 0.875em;
      p {
        font-size: 0.9375em;
        line-height: 140%;
        color: #A7A7A7;;
      }
      a {
        text-transform: uppercase;
        &:hover {
          color: #43B64B;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 0;
    &__inner {
      justify-content: start;
      position: relative;
      padding: 2.8125em 0 8.4375em;
    }
    &__logo {
      width: 5.875em;
      margin-right: 3.75em;
    }
    &__nav {
      &:not(:first-child) {
        margin-left: 3.75em;
      }
    }
    &__brevis {
      position: absolute;
      left: 50%;
      bottom: 2.8125em;
      transform: translate(-50%, 0);
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__logo {
      margin-right: 1.25em;
    }
    &__nav {
      &:not(:first-child) {
        margin-left: 1.875em;
      }
      &-title {
        span {
          font-size: 0.875em;
        }
      }
    }
    &__list {
      li {
        font-size: 0.875em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__inner {
      flex-direction: column;
      padding: 1.875em 0 7.1875em;
    }
    &__logo {
      margin: 0 auto 1.25em;
    }
  }
}

@media screen and (max-width: 451px) {
   .footer {
     &__items {
       display: grid;
       grid-template-columns: repeat(2, calc(50% - 0.625em / 2));
       gap: 1.5625em 0.625em;
     }
     &__nav {
       &:not(:first-child) {
         margin: 0;
       }
     }
   }
}