.product-info {
  padding: 14.0625em 0 2em;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 14.0625em;
    background-image: url("../img/product-info__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 16em;
    right: -28.375em;
    width: 51.5em;
    height: 51.5em;
    border-radius: 50%;
    background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(67, 182, 75, 0.22) 0%, rgba(67, 182, 75, 0.00) 100%);
    pointer-events: none;
  }
  &__inner {
    margin-top: 3.75em;
  }
  .breadcrumbs__item {
    color: #272B2D;
    &--active {
      color: #BDBDBD;
    }
    &:not(:last-child) {
      &:after {
        background: #272B2D;;
      }
    }
  }
  &__title {
    margin-top: 2.1875em;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 3.125em;
  }
  &__left {
    width: 39.5em;
    margin-right: 2.5em;
    border: 0.0625em solid #BDBDBD;
    background: #FFF;
    padding: 0.75em;
  }
  &__right {
    width: calc(100% - 42em);
  }
  &__img {
    width: 20em;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 100%;
    }
  }
  &__article {
    font-size: 1.125em;
    line-height: 120%;
    color: #777;
    padding-bottom: 0.75em;
    position: relative;
    span {
      color: #272B2D;
    }
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 13.5625em;
      height: 0.0625em;
      background: #BDBDBD;
    }
  }
  &__subtitle {
    margin-top: 0.9375em;
    span {
      font-size: 1.375em;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__list {
    margin-top: 0.9375em;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 0.0625em;
        border-bottom: 0.0625em dashed #BDBDBD;
      }
      &:not(:first-child) {
        margin-top: 1.125em;
      }
      span:nth-child(1) {
        font-size: 0.9375em;
        line-height: 120%;
        color: #272B2D;
        padding-right: 0.1875em;
        overflow: hidden;
        position: relative;
        z-index: 1;
      }
      span:nth-child(2) {
        text-align: right;
        font-size: 0.9375em;
        line-height: 120%;
        font-weight: 500;
        color: #000000;
        margin-left: 1em;
        padding-left: 0.1875em;
        overflow: hidden;
        position: relative;
        z-index: 1;
      }
    }
  }
  &__btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 3.125em;
  }
  &__btn {
    width: 15.25em;
    height: 3.5em;
    &:hover {
      span {
        color: #ffffff;
      }
    }
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #272B2D;
    }
  }
  &__btn2 {
    width: 15.25em;
    height: 3.5em;
    margin-left: 1.365em;
    span {
      font-size: 1.0625em;
      line-height: 120%;
    }
  }
}
.products2 {
  padding: 3.125em 0;
  &__inner {
    position: relative;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__slider {
    margin-top: 2.5em;
  }
  &__slider-btns {
    display: flex;
    align-items: center;
  }
  .swiper-button-prev {
    position: static;
    margin-top: 0;
  }
  .swiper-button-next {
    position: static;
    margin-top: 0;
    margin-left: 0.625em;
  }
}
.products-info {
  padding-bottom: 3.125em;
  &__tabs {
    margin-top: 3.75em;
    &-head {
      display: flex;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0.0625em;
        background: #43B64B;
      }
    }
    &-body {
      margin-top: 2.8125em;
    }
  }
  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 10.625em;
    height: 3.25em;
    background: transparent;
    cursor: pointer;
    &--active {
      border: 0.0625em solid #43B64B;
    }
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #000000;
    }
  }
  &__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__documents {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 5.625em / 2));
    gap: 3.125em 5.625em;
    max-width: 41.625em;
  }
  &__document {
    display: flex;
    align-items: flex-end;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0.6875em;
      bottom: 0;
      width: 100%;
      height: 0.0625em;
      background: #43B64B;
    }
    &-img {
      width: 5.1875em;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 1.5625em;
      padding: 0.625em 0;
      span {
        font-size: 1.0625em;
        line-height: 120%;
        color: #000000;
      }
    }
  }
  &__subtitle {
    span {
      font-size: 1.25em;
      line-height: 140%;
      color: #272B2D;
    }
  }
  &__form {
    &-grid {
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 4.6875em / 4));
      gap: 1.5625em;
      margin-top: 1.125em;
    }
    &-group {
      textarea {
        height: 100% !important;
      }
    }
    ._big {
      grid-column: 5/3;
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 1.5625em;
    }
    &-info {
      p {
        font-size: 1em;
        line-height: 140%;
        color: #303436;
      }
    }
    &-btn {
      flex-shrink: 0;
      width: 15.25em;
      height: 3.5em;
      margin-left: 2em;
      span {
        font-size: 1.0625em;
        line-height: 120%;
      }
    }
  }
  &__text {
    p {
      font-size: 0.9375em;
      line-height: 140%;
      color: #000000;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 5em;
    max-width: 65.625em;
    margin-top: 3.125em;
  }
  &__item {
    display: flex;
    align-items: center;
    min-height: 5.1875em;
    padding: 0.625em 3.125em 0.625em 5.625em;
    background: #ffffff;
    position: relative;
    &-number {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width:4.6875em;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 2em;
        line-height: 140%;
        color: #ffffff;
        padding-right: 0.5em;
      }
      i {
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
    &-text {
      p {
        font-size: 1.1875em;
        line-height: 140%;
        color: #000000;
      }
    }
  }
}
.table {
  margin-top: 6.25em;
  &__wrap {
    overflow-x: auto;
    padding-bottom: 1.25em;
    &::-webkit-scrollbar {
      width: 100%;
      height: 1em;
    }
    &::-webkit-scrollbar-track {
      background: #EAEAEA;
    }
    &::-webkit-scrollbar-thumb {
      background: #43B64B;
    }
  }
  table {
    min-width: 93.75em;
    width: 100%;
    text-align: center;
    border-collapse: collapse;
    margin-top: 2.1875em;
    tbody tr {
      &:nth-child(odd) {
        td {
          background: #EAEAEA;
        }
      }
      &:nth-child(even) {
        td {
          background: #ffffff;
        }
      }
    }
    tr {
      td:nth-child(1) {
        font-size: 1em;
        font-weight: 400;
      }
      &:hover {
        td {
          font-weight: 700;
        }
      }
    }
    td, th {
      border: 0.25em solid #F2F2F2;
    }
    th {
      font-size: 1em;
      line-height: 140%;
      color: #272B2D;
      background: #ffffff;
      padding: 1em;
    }
    td {
      font-size: 1.5625em;
      line-height: 140%;
      font-weight: 300;
      color: #272B2D;
      padding: 1em;
    }
    .rowspan3 {
      width: 18.75em;
      font-family: 'OpenSans';
      background: #43B64B;
      padding: 3.75em 1.25em;
      color: #ffffff;
      text-transform: uppercase;
    }
    .rowspan6 {
      font-family: 'OpenSans';
      background: #43B64B;
      padding: 1.25em;
      color: #ffffff;
      text-transform: uppercase;
    }
  }
  &__info {
    min-width: 93.75em;
    width: 100%;
    background: #FFF;
    padding: 0.9375em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.5625em;
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #BDBDBD;
      &:not(:first-child) {
        margin-left: 1.5em;
      }
    }
  }
  &__desc {
    margin-top: 2.1875em;
  }
  &__text {
    p {
      font-size: 0.9375em;
      line-height: 140%;
      color: #272B2D;
    }
  }
  &__list {
    margin-top: 1em;
    li {
      font-size: 0.9375em;
      line-height: 140%;
      color: #272B2D;
      padding-left: 1.875em;
      position: relative;
      &:not(:first-child) {
        margin-top: 0.5;
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 1.25em;
        height: 0.0625em;
        background: #43B64B;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .product-info {
    padding: 11.875em 0 2em;
    &:after {
      height: 11.875em;
    }
    &__inner {
      margin-top: 2.1875em;
    }
    &__content {
      align-items: start;
      margin-top: 1.875em;
    }
    &__left {
      width: 26.25em;
      margin-right: 0.625em;
    }
    &__right {
      width: calc(100% - 26.875em);
    }
    &__img {
      width: 13.125em;
    }
  }
  .products2 {
    padding: 1.5625em 0 3.125em;
    &__slider {
      margin-top: 1.5625em;
    }
  }
  .products-info {
    padding-bottom: 1.5625em;
    &__form {
      &-grid {
        grid-template-columns: repeat(2, calc(50% - 3.125em / 2));
        gap: 1.5625em 3.125em;
      }
      ._big {
        grid-column: 1/3;
      }
    }
    &__item {
      padding-right: 0.625em;
    }
  }
}

@media screen and (max-width: 768px) {
  .product-info {
    padding-top: 7.8125em;
    &:after {
      height: 7.8125em;
    }
    &__left {
      width: 12.8125em;
    }
    &__right {
      width: calc(100% - 13.4375em);
    }
    &__img {
      width: 6.5625em;
    }
  }
  .products-info {
    &__form {
      &-grid {
        grid-template-columns: repeat(2, calc(50% - 1.5625em / 2));
        gap: 1.5625em;
      }
    }
    &__item {
      &-text {
        p {
          font-size: 0.9375em;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .product-info {
    padding-top: 6.25em;
    &:after {
      height: 6.25em;
    }
    &__inner {
      margin-top: 1.25em;
    }
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 1.5625em 0;
    }
    &__right {
      width: 100%;
    }
    &__img {
      width: 17.5em;
    }
    &__btns {
      justify-content: space-between;
    }
  }
  .products2 {
    &__top {
      flex-direction: column;
    }
  }
  .products-info {
    &__documents {
      grid-template-columns: 100%;
      gap: 3.125em;
      max-width: 17.1875em;
    }
    &__document {
      &-img {
        width: 4.375em;
      }
    }
    &__subtitle {
      span {
        font-size: 0.875em;
      }
    }
    &__form {
      &-info {
        p {
          font-size: 0.875em;
        }
      }
      &-btn {
        margin-left: 0.875em;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .product-info {
    &__img {
      width: 11.9375em;
    }
    &__list {
      li {
        align-items: flex-end;
        span:nth-child(1) {
          font-size: 0.875em;
        }
        span:nth-child(2) {
          font-size: 0.875em;
        }
      }
    }
    &__btn2 {
      margin-left: 0.5em;
    }
  }
  .products-info {
    &__form {
      &-grid {
        grid-template-columns: 100%;
      }
      &-group {
        textarea {
          height: 6.875em !important;
        }
      }
      ._big {
        grid-column: unset;
      }
      &-bottom {
        display: block;
      }
      &-btn {
        margin: 1.5625em auto 0;
      }
    }
    &__item {
      &-number {
        span {
          font-size: 1.5625em;
        }
      }
      &-text {
        p {
          font-size: 0.875em;
        }
      }
    }
  }
}