.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.btn-green {
  border: 0.0625em solid #43B64B;
  &:hover {
    background: #43B64B;
  }
}

.btn-black {
  background: #303436;
  span {
    color: #FFFFFF;
  }
  &:hover {
    background: #FFFFFF;
    border: 0.0625em solid #43b64b;
    span {
      color: #272B2D;
    }
  }
}

.form {
  &-group {
    border: none;
    padding: 0;
    margin: 0;
    position: relative;
    img {
      position: absolute;
      top: 1.1875em;
      left: 1.25em;
      max-width: 1.5625em;
      width: 100%;
    }
    input {
      width: 100%;
      height: 3.375em;
      font-size: 1.125em;
      line-height: 120%;
      color: #000000;
      background: #ffffff;
      border: 0.0625em solid #BDBDBD;
      padding: 0 0.9375em 0 4em;
      &::placeholder {
        color: #303436;
      }
    }
    textarea {
      resize: none;
      width: 100%;
      height: 5em;
      font-size: 1.125em;
      line-height: 120%;
      color: #000000;
      background: #ffffff;
      border: 0.0625em solid #BDBDBD;
      padding: 0.9375em 0.9375em 0 4em;
      &::placeholder {
        color: #303436;
      }
    }
  }
}

.swiper-pagination {
  &-bullet {
    width: 0.625em;
    height: 0.625em;
    background: #BDBDBD;
    opacity: 1;
  }
  &-bullet-active {
    background: #43B64B;
  }
}

.display2 {
  text-align: center;
  font-size: 1.875em;
  line-height: 120%;
  font-weight: 400;
  color: #272B2D;
  i {
    max-width: 1.25em;
    display: block;
    margin: 0.3125em auto 0;
    img {
      width: 100%;
    }
  }
}

.display3 {
  display: flex;
  align-items: center;
  font-size: 1.5625em;
  line-height: 140%;
  font-weight: 700;
  color: #272B2D;
  i {
    flex-shrink: 0;
    width: 1.5625em;
    margin-right: 0.5em;
    img {
      width: 100%;
    }
  }
  span {

  }
}

.swiper-button-next,
.swiper-button-prev {
  width: 100%;
  &:after {
    display: none;
  }
}

.nav {
  padding: 3.125em 1.25em;
  &__list {
    li {
      font-size: 1.5em;
      line-height: 120%;
      color: #000000;
      &:not(:first-child) {
        margin-top: 1em;
      }
      &:hover {
        color: #43B64B;
      }
    }
  }
  &__btn {
    width: 300px;
    height: 56px;
    &:hover {
      span {
        color: #ffffff;
      }
    }
  }
}

.breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    font-size: 1.125em;
    line-height: 120%;
    color: #ffffff;
    margin-bottom: 1em;
    position: relative;
    &--active {
      color: #BDBDBD;
    }
    &:not(:last-child) {
      margin-right: 2.1875em;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -1.875em;
        transform: translate(0, -50%);
        width: 1.5625em;
        height: 0.0625em;
        background: #ffffff;
      }
    }
  }
}

.modal {
  background: rgba(#000, .4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 28.125em;
    max-width: 100%;
    position: relative;
    background: #ffffff;
    padding: 2.5em;
    margin: 0 auto;
    &:after {
      content: '';
      position: absolute;
      top: 2.5em;
      right: -21.5em;
      width: 34.1875em;
      height: 34.1875em;
      border-radius: 50%;
      background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(67, 182, 75, 0.22) 0%, rgba(67, 182, 75, 0.00) 100%);
      pointer-events: none;
    }
    @media screen and (max-width: 992px) {
      margin: 3.125em auto 0;
      max-width: calc(100% - 1.875em);
    }
  }
  &__close {
    position: absolute;
    top: -2em;
    right: -2em;
    background: transparent;
    padding: 0;
    margin: 0;
  }
  &__content {
    text-align: center;
    position: relative;
    z-index: 2;
  }
  &__title {
    span {
      font-size: 1.4375em;
      line-height: 120%;
      color: #272B2D;
    }
  }
  &__form {
    margin-top: 2.1875em;
    &-wrap {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.5625em;
    }
    &-info {
      margin-top: 1.5625em;
      text-align: start;
      p {
        font-size: 0.875em;
        line-height: 120%;
        color: #303436;
      }
    }
    &-btn {
      width: 15.25em;
      height: 3.5em;
      margin: 1.5625em auto 0;
      span {
        font-size: 1.0625em;
        line-height: 120%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .display2 {
    font-size: 1.5625em;
  }

  .display3 {
    font-size: 1.375em;
  }

  .form {
    &-group {
      img {
        left: 1em;
        max-width: 1.25em;
      }
      input {
        font-size: 1em;
        padding-left: 2.8125em;
      }
      textarea {
        left: 1em;
        font-size: 1em;
        padding-left: 2.8125em;
      }
    }
  }
}