.content {
  overflow: hidden;
}
.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 21.1875em 0 13.375em;
  overflow: hidden;
  &__slider {
    position: relative;
  }
  &__swiper {
    overflow: unset;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active {
      opacity: 1;
      pointer-events: all;
    }
  }
  &-slide {
    display: flex;
    align-items: center;
    &__left {
      width: 42.1875em;
      margin-right: 6.25em;
      padding: 5em 3.125em;
      position: relative;
    }
    &__right {
      width: 38em;
      img {
        width: 100%;
      }
    }
    &__left-bg {
      position: absolute;
      top: 0;
      right: 0;
      width: 55.3125em;
      height: 100%;
      background: rgba(48, 52, 54, 0.9);
      clip-path: polygon(0% 0%, 90% 0, 100% 50%, 90% 100%, 0% 100%);    }
  }
  &__desc {
    position: relative;
    z-index: 1;
  }
  &__title {
    font-family: 'OpenSans';
    font-size: 2.5em;
    font-weight: 700;
    text-transform: uppercase;
    color: #43B64B;
  }
  &__text {
    margin-top: 2.125em;
    p {
      font-size: 1.375em;
      line-height: 140%;
      color: #ffffff;
    }
  }
  &__btn {
    height: 3.5em;
    padding: 0 3.4375em;
    margin-top: 3.75em;
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #ffffff;
    }
  }
  &__slider-nav {
    position: absolute;
    left: 23.6875em;
    bottom: 5em;
    height: 3.5em;
    display: flex;
    align-items: center;
  }
  .swiper-button-prev {
    position: static;
    z-index: 1;
    width: 100%;
    margin: 0;
    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    position: static;
    z-index: 1;
    width: 100%;
    margin: 0 0 0 0.625em;
    &:after {
      display: none;
    }
  }
}
.advantages {
  padding: 0 0 3.125em;
  position: relative;
  margin-top: -7.5em;
  overflow: hidden;
  &__bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120em;
    height: 25.375em;
    background: radial-gradient(50.09% 50.09% at 49.91% 50.09%, rgba(67, 182, 75, 0.21) 0%, rgba(67, 182, 75, 0.00) 100%);
    border-radius: 120em;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__slider {
    position: relative;
  }
  @media screen and (min-width: 769px) {
    &__swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(6,calc((100% / 6) - (1.875em * 5 / 6)));
      gap: 1.875em;
      transform: unset !important;
    }
    .swiper-slide {
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next,
    .swiper-pagination {
      display: none;
    }
  }
  &__slide {
    &-top {
      min-height: 16.4375em;
      display: flex;
      flex-direction: column;
      text-align: center;
      flex-grow: 1;
      background: #FFF;
      clip-path: polygon(50% 0%, 100% 40%, 100% 100%, 0 100%, 0 40%);
      padding: 5em 0.75em 0.625em;
    }
    &-icon {
      max-width: 5.375em;
      margin: 0 auto 1.25em auto;
      img {
        width: auto;
      }
    }
    &-title {
      margin-top: auto;
      span {
        font-family: 'OpenSans';
        font-size: 1.125em;
        line-height: 140%;
        font-weight: 700;
        text-transform: uppercase;
        color: #43B64B;
      }
    }
    &-text {
      text-align: center;
      margin-top: 0.625em;
      p {
        font-size: 0.875em;
        line-height: 140%;
        color: #303436;
      }
    }
  }
}
.directions {
  padding: 3.125em 0 7.8125em;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    right: -31.25em;
    bottom: 0;
    width: 56.25em;
    height: 56.25em;
    border-radius: 56.25em;
    background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(67, 182, 75, 0.22) 0%, rgba(67, 182, 75, 0.00) 100%);
  }
  &__slider {
    position: relative;
    margin-top: 2.8125em;
  }
  @media screen and (min-width: 577px) {
    &__swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(3,calc((100% / 3) - (7.8125em * 2 / 3)));
      gap: 7.8125em;
      transform: unset !important;
    }
    .swiper-slide {
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next,
    .swiper-pagination {
      display: none;
    }
  }
  &__slide {
    &-top {
      position: relative;
      padding: 1.5625em 1.875em;
      border: 0.0625em solid #43B64B;
    }
    &-img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, #FFF 0%, rgba(255, 255, 255, 0.00) 100%);
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-title {
      max-width: 12.0625em;
      position: relative;
      z-index: 1;
      span {
        font-size: 1.375em;
        line-height: 140%;
        color: #000000;
      }
    }
    &-bottom {
      padding: 1.5em 1.5em 1.5em 4.6875em;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 1.875em;
        width: 0.0625em;
        height: 100%;
        background: #000000;
      }
    }
    &-list {
      li {
        font-size: 1em;
        line-height: 120%;
        color: #000000;
        position: relative;
        padding-left: 1.5em;
        &:not(:first-child) {
          margin-top: 0.625em;
        }
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 0.5625em;
          height: 0.0625em;
          background: #43B64B;
        }
      }
    }
    &-btn {
      padding: 0;
      margin-top: 0.9375em;
      span {
        font-size: 1.0625em;
        line-height: 120%;
        color: #43B64B;
      }
    }
  }
}
.products {
  background-image: url("../img/products-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  padding: 7.8125em 0 3.125em;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: -2em;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 30.3125em;
    background: linear-gradient(180deg, #F2F2F2 0%, rgba(255, 255, 255, 0.00) 100%);
    filter: blur(0.46875em);
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__tabs {
    margin-top: 3.75em;
    &-head {
      display: flex;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0.0625em;
        background: #43B64B;
      }
    }
    &-body {
      margin-top: 5em;
    }
  }
  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10.625em;
    height: 3.25em;
    background: transparent;
    cursor: pointer;
    &--active {
      border: 0.0625em solid #43B64B;
    }
    span {
      font-size: 1.0625em;
      line-height: 120%;
      color: #000000;
    }
  }
  &__slider {
    position: relative;
    display: none;
    &--active {
      display: block;
    }
  }
  &__btn {
    width: 16.625em;
    height: 3.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #272B2D;
    margin: 2.8125em auto 0;
    &:hover {
      background: #FFFFFF;
      border: 0.0625em solid #43b64b;
      span {
        color: #272B2D;
      }
    }
    span {
      font-size: 1.125em;
      line-height: 120%;
      color: #ffffff;
    }
  }
  @media screen and (min-width: 993px) {
    &__swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (4.375em * 3 / 4)));
      gap: 4.375em;
      transform: unset !important;
    }
    .swiper-slide {
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next,
    .swiper-pagination {
      display: none;
    }
  }
}
.product-card {
