      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0.3125em;
        width: 100%;
        height: 0.0625em;
        border-bottom: 0.0625em dashed #BDBDBD;
      }
      &:not(:first-child) {
        margin-top: 0.9375em;
      }
      span:nth-child(1) {
        font-size: 0.875em;
        line-height: 120%;
